	export default {
		data() {
			return {
				SessionKey: '',
				OpenId: '',
				nickName: null,
				avatarUrl: null,
				isCanUse: uni.getStorageSync('isCanUse') || true //默认为true
			};
		},
		methods: {
			//第一授权获取用户信息===》按钮触发
			wxGetUserInfo() {
				let _this = this;
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
                      
						let nickName = infoRes.userInfo.nickName; //昵称
						let avatarUrl = infoRes.userInfo.avatarUrl; //头像
						try {
							uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
							_this.updateUserInfo(infoRes);
						} catch (e) {}
					},
					fail(res) {}
				});
			},

			//登录
			login() {
				let _this = this;
				// uni.showLoading({
				//     title: '登录中...'
				// });

				// 1.wx获取登录用户code
				uni.login({
					provider: 'weixin',
					success: (loginRes)=>{
						let code = loginRes.code;
						this.$http.wxlogin(loginRes.code).then(res => {
							uni.setStorageSync('shareid', res.data.id);//分享id
							uni.setStorageSync('token', res.data.token);
							uni.setStorageSync('role', res.data.role);
							uni.setStorageSync('sessionKey', res.data.sessionKey);
							if (!_this.isCanUse) {
								//非第一次授权获取用户信息
								uni.getUserInfo({
									provider: 'weixin',
									success: function(infoRes) {
										//获取用户信息后向调用信息更新方法
										let nickName = infoRes.userInfo.nickName; //昵称
										let avatarUrl = infoRes.userInfo.avatarUrl; //头像
										console.log(nickName);
										_this.updateUserInfo(infoRes); //调用更新信息方法
										// uni.hideLoading();

									}
								});
							}
						})
					
					},
				});
			},
			//向后台更新信息
			updateUserInfo(userinfo) {
				console.log(userinfo);
				let {nickName,avatarUrl}=userinfo.userInfo;
					uni.setStorageSync('nickName', nickName);//
					uni.setStorageSync('avatarUrl', avatarUrl);//分享id
				uni.getStorage({
    key: 'token',
    success:  (res)=>{
        console.log(res.data);
		this.$http.info(userinfo,uni.getStorageSync('sessionKey'),res.data)
		
		uni.redirectTo({ //信息更新成功后跳转到小程序首页
			url: '/pages/index/index'
		});
    }
});
			
			}
		},
		onLoad() { //默认加载

			this.login();
		}
	}
